import React, { Component } from "react";
import rehypeReact from 'rehype-react';
import { Carousel } from "react-responsive-carousel";

import "react-responsive-carousel/lib/styles/carousel.min.css";
import '../templates/post-template.css';

const renderAst = new rehypeReact({
  createElement: React.createElement,
}).Compiler;


class TextPostBody extends Component {
  render() {
    const { htmlAst, images } = this.props;
    if (images) {
      return (
        <div className="post-content">
            <div className="content-image">
                <Carousel showStatus={false} swipeable emulateTouch useKeyboardArrows> 
                  {images.edges.map((image, i) => <img src={`/static/${image.node.relativePath}`} key={i}/>)}
                </Carousel>
            </div>
            <div className="content-body">
                {renderAst(htmlAst)}
            </div>
        </div>
      )
    } else {
      return (
        <div className="post-content">
            <div className="content-image">
            </div>
            <div className="content-body">
                {renderAst(htmlAst)}
            </div>
        </div>
      )
    }
  }
}

export default TextPostBody;


